.Card {
    
    margin: 10px;
    border: 2px  rgba(145, 242, 255, 0.3);
    border-radius: 10px;
    background-color: #ffffff;
    text-align: center;
    
    overflow: auto;
    flex: 1;
    display: flex;
    flex-direction: column;
    min-width: 300px; /*Tamanho minimo do card*/
}

.Card .Conteudo {
    flex: 1;
    padding: 15px 0px;
    color: #000000;
}

.Card .Footer {
    background-color: #c0e4e2;
    color: #ffffff;
    padding: 5px 0px;
    text-align: justify;
    font-size: 9pt;
}